import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { IntegratedApplication } from '../../../blocks/models/integrated-application';

@Component({
  selector: 'app-ia-imanage',
  templateUrl: './ia-imanage.component.html',
  styleUrls: ['./ia-imanage.component.scss'],
})
export class IaIManageComponent implements OnInit {
  @Input() application: IntegratedApplication;

  url: string;

  fieldUrl: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.fieldUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-integratedapplication.imanage.url'),
      placeholder: this.translateService.instant('admin-integratedapplication.imanage.url'),
      required: true,
    });

    this.url = (this.application.data.get('url')?.value as string) ?? '';
  }

  onFieldChanged = (key: string, value: string): void => {
    this.application.data.set(key, value);
  };
}
