import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Role } from 'shared-code-lib';
import { IntegratedApplication } from '../../../blocks/models/integrated-application';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-ia-roles',
  templateUrl: './ia-roles.component.html',
  styleUrls: ['./ia-roles.component.scss'],
})
export class IaRolesComponent implements OnInit {
  @Input() application: IntegratedApplication;
  @Output() roleIdsChange = new EventEmitter<Array<string>>();

  available: Array<Role> = [];
  current: Array<Role> = [];

  constructor(private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.loadPicklistItems();
  }

  currentChanged = (items: Array<Role>) => {
    this.application.roleIds = [];
    for (let item of items) {
      this.application.roleIds.push(item.id);
    }

    this.roleIdsChange.emit(this.application.roleIds);
  };

  loadPicklistItems = () => {
    this.hydraApi.getRoles(this.loaded, this.loadFailed);
  };

  loaded = (roles: Array<Role>) => {
    this.available = roles;
    this.current = roles.filter((r) => this.application.roleIds.find((ri) => ri == r.id) != null);
  };

  loadFailed = () => {};
}
