import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { IntegratedApplication } from '../../../blocks/models/integrated-application';

@Component({
  selector: 'app-ia-onedrive',
  templateUrl: './ia-onedrive.component.html',
  styleUrls: ['./ia-onedrive.component.scss'],
})
export class IaOnedriveComponent implements OnInit {
  @Input() application: IntegratedApplication;

  clientId: string;
  tenantName: string;

  fieldClientId: FieldDefinition;
  fieldTenantName: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.fieldClientId = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-integratedapplication.onedrive-clientid'),
      placeholder: this.translateService.instant('admin-integratedapplication.onedrive-clientid'),
      required: true,
    });

    this.fieldTenantName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-integratedapplication.onedrive-tenantname'),
      placeholder: this.translateService.instant('admin-integratedapplication.onedrive-tenantname'),
      required: true,
    });

    const mdClientId = this.application.data.get('clientId');
    this.clientId = mdClientId ? <string>mdClientId.value : '';

    const mdTenantName = this.application.data.get('tenantName');
    this.tenantName = mdTenantName ? <string>mdTenantName.value : '';
  }

  onClientIdChanged = (value: string): void => {
    this.application.data.set('clientId', value);
  };

  onTenanNameChanged = (value: string): void => {
    this.application.data.set('tenantName', value);
  };
}
