import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { Tab } from 'shared-code-lib';
import { IntegratedApplication } from '../../blocks/models/integrated-application';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';

@Component({
  selector: 'app-admin-integratedapplications',
  templateUrl: './admin-integratedapplications.component.html',
  styleUrls: ['./admin-integratedapplications.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminIntegratedApplicationsComponent implements OnInit, OnDestroy {
  constructor(
    private translateService: TranslateService,
    private userState: UserStateService,
    private router: Router,
    private hydraApi: HydraApiService,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService
  ) {}

  hasAdvanced = false;
  activeTab = 'roles';
  tabs = new Array<Tab>();

  application: IntegratedApplication;

  saveButton: Control;
  cancelButton: Control;
  formControls: Array<Control> = [];

  initialEnableState: boolean;

  subscriptions = new Subscription();

  ngOnInit() {
    this.spinner.hide();
    this.createButtons();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        if (data.type == 'properties') {
          this.hydraApi.getIntegratedApplication(params.id, this.appLoaded, this.appLoadFailed);
        }
      })
    );
  }

  createTabs = (): void => {
    this.tabs.push(new Tab('roles', this.translateService.instant('admin-integratedapplication.roles')));
    if (this.hasAdvanced)
      this.tabs.push(new Tab('advanced', this.translateService.instant('admin-integratedapplication.advanced')));
  };

  private appLoaded = (app: IntegratedApplication) => {
    this.application = app;

    this.hasAdvanced = app.application == 'FileBound' || app.application == 'OneDrive' || app.application == 'IManage';

    this.initialEnableState = app.enabled;

    this.createTabs();
  };

  private appLoadFailed = () => {
    //todo: toastr
  };

  onSave = () => {
    if (this.application.application == 'FileBound' && this.isInValidateFileBoundUrl()) {
      this.toastr.error(this.translateService.instant('admin-integratedapplication.emptyFileBoundUrl'));
      return;
    }

    if (this.application.application == 'OneDrive' && this.isInvalidOneDriveDetails()) {
      this.toastr.error(this.translateService.instant('admin-integratedapplication.emptyOneDriveDetails'));
      return;
    }

    if (this.application.application == 'IManage' && this.isInvalidIManageDetails()) {
      this.toastr.error(this.translateService.instant('admin-integratedapplication.imanage.emptyUrl'));
      return;
    }

    this.saveIntegratedApplication();
  };

  isInValidateFileBoundUrl = (): boolean => {
    return !this.application.data.get('fileBoundUrl') || !this.application.data.get('fileBoundUrl').value;
  };

  isInvalidOneDriveDetails = (): boolean => {
    return (
      !this.application.data.get('clientId') ||
      !this.application.data.get('clientId').value ||
      !this.application.data.get('tenantName') ||
      !this.application.data.get('tenantName').value
    );
  };

  isInvalidIManageDetails = (): boolean => {
    return !this.application.data.get('url') || !this.application.data.get('url').value;
  };

  saveIntegratedApplication = () => {
    this.hydraApi.updateIntegratedApplication(this.application, this.appSaved, this.appSaveFailed);
    this.spinner.showSavingSpinner();
  };

  appSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translateService.instant('admin-integratedapplication.saved'));

      if (this.initialEnableState != this.application.enabled) {
        this.userState.loadAppIntegrations(
          () => {},
          () => {}
        );
      }

      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-integratedapplication.validation-errors');
    }

    this.spinner.hide();
  };

  appSaveFailed = (err: any) => {
    this.toastr.error(this.translateService.instant('admin-integratedapplication.save-failed'));
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.IntegratedApplications'
    ); // todo - handle this "better"
  };

  private createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translateService.instant('admin-user.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );
    this.cancelButton = new Control(
      this.onCancel,
      this.translateService.instant('admin-user.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);
  }

  activateTab = (view) => {
    this.activeTab = view;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
