import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminIntegratedApplicationsComponent } from './admin-integratedapplications.component';

const routes: Routes = [
  {
    path: ':id',
    component: AdminIntegratedApplicationsComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminIntegratedApplicationsRoutingModule {}
