import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { IntegratedApplication } from '../../../blocks/models/integrated-application';

@Component({
  selector: 'app-ia-filebound',
  templateUrl: './ia-filebound.component.html',
  styleUrls: ['./ia-filebound.component.scss'],
})
export class IaFileboundComponent implements OnInit {
  @Input() application: IntegratedApplication;

  fileBoundUrl: string;

  fieldFileBoundUrl: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.fieldFileBoundUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-integratedapplication.fileBoundUrl'),
      placeholder: this.translate.instant('admin-integratedapplication.fileBoundUrl'),
      required: true,
    });

    const md = this.application.data.get('fileBoundUrl');
    this.fileBoundUrl = md ? <string>md.value : '';
  }

  onFileBoundUrlChanged = (value: string): void => {
    this.application.data.set('fileBoundUrl', value);
  };
}
